body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
    background-color: black;
    min-height: 100vh;
    padding: 10px 0px;
}
.github-button {
    color: white;
    position: absolute;
    right: 10px;
    top: 5px;
    font-size: 25px;
    cursor: pointer;
}
.row {
    color: white;
    display: flex;
    margin: 60px 0px;
    position: relative;
}
.seiyu-image-panel {
    width: 500px;
}
.seiyu-roles {
    display: flex;
    min-width: calc(100vw - 225px);
    overflow-x: scroll;
}

.seiyu-ranking {
    position: absolute;
    background-color: rgba(0,0,0,.6);
    top: 10px;
    left: 10px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    border-radius: 50%;
    z-index: 11;
}

.seiyu-external {
    top: 10px;
    right: 10px;
    color: rgba(0,0,0,.6);
}
.image-panel {
    min-width: 75px;
    position: relative;
}

.panel-image {
    min-height: 400px;
    max-height: 100vh;
    width: 100%;
    object-fit: cover;
    box-shadow: -3px 0px 10px 1px black;
}

.character-panel {
    flex-grow: 2;
}

.character-panel-hover {
    width: 250px;
    height: 400px;
    object-fit: cover;
}

.image-panel-hover {
    z-index: 10;
}

.image-panel-label {
    position: absolute;
    width: 100%;
    bottom: 0px;
    text-align: center;
    background-color: rgba(0, 0, 0, .6);
    padding: 10px 0px;
}

.image-panel-description {
    padding: 10px;
    color: white;
    position: absolute;
    width: 200px;
    height: calc(100% - 20px);
    background-color: rgba(0, 0, 0, .9);
    z-index: 99999;
    top: 0px;
}

.image-panel-description-left {
    right: 100%;
}

.image-panel-description-right {
    left: 225px;
}

.image-panel-description-name {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    margin-top: 50%;
}

.image-panel-external-link {
    cursor: pointer;
    margin-top: 10px;
    padding: 5px 5px;
    border-radius: 5px;
    font-size: 14px;
    border: 2px solid white;
    -webkit-transition: all .25s linear;
    transition: all .25s linear;
    color: white;
}

.image-panel-external-link:hover {
    background-color: white;
    color: black !important;
}
.image-panel-external-link:hover a{
    color: black !important;
}

a {
    text-decoration: none;
    color: white;
    -webkit-transition: color .25s linear;
    transition: color .25s linear;
}
