.image-panel {
    min-width: 75px;
    position: relative;
}

.panel-image {
    min-height: 400px;
    max-height: 100vh;
    width: 100%;
    object-fit: cover;
    box-shadow: -3px 0px 10px 1px black;
}

.character-panel {
    flex-grow: 2;
}

.character-panel-hover {
    width: 250px;
    height: 400px;
    object-fit: cover;
}

.image-panel-hover {
    z-index: 10;
}

.image-panel-label {
    position: absolute;
    width: 100%;
    bottom: 0px;
    text-align: center;
    background-color: rgba(0, 0, 0, .6);
    padding: 10px 0px;
}

.image-panel-description {
    padding: 10px;
    color: white;
    position: absolute;
    width: 200px;
    height: calc(100% - 20px);
    background-color: rgba(0, 0, 0, .9);
    z-index: 99999;
    top: 0px;
}

.image-panel-description-left {
    right: 100%;
}

.image-panel-description-right {
    left: 225px;
}

.image-panel-description-name {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    margin-top: 50%;
}

.image-panel-external-link {
    cursor: pointer;
    margin-top: 10px;
    padding: 5px 5px;
    border-radius: 5px;
    font-size: 14px;
    border: 2px solid white;
    transition: all .25s linear;
    color: white;
}

.image-panel-external-link:hover {
    background-color: white;
    color: black !important;
}
.image-panel-external-link:hover a{
    color: black !important;
}

a {
    text-decoration: none;
    color: white;
    transition: color .25s linear;
}